.first-screen {
    position: relative;
    .first-screen-nav {
        display: flex;
        max-width: 1568px;    
        width: 100%;
        justify-content: space-between;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
    }
}

.first-screen-slider {
    .container {
        padding-top: 164px;
        padding-bottom: 92px;
    }

    .slick-slide {
        background-size: cover;
        background-repeat: no-repeat;
        padding-bottom: 0;
    }

    .slick-dots {
        position: absolute;
        left: 50%;
        transform: translate(-50%,0);
        bottom: 42px;
    }
}

.first-screen-title {
    font-size: 56px;
    font-style: normal;
    font-weight: 700;
    line-height: 126%; /* 70.56px */
    text-transform: uppercase;
    max-width: 463px;
    margin-bottom: 8px;
}

.first-screen-subtitle {
    font-size: 20px;
    font-style: normal;
    font-weight: 300;
    line-height: 135.5%; /* 27.1px */
    max-width: 345px;
    margin-bottom: 17px;
}