section {
    margin-top: 0;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: $section-margin-lg;
    max-width: 1920px;
    
 

    &:last-of-type {
        margin-bottom: 56px;
    }

    &.container-fluid {
        padding: 90px 85px 100px;
        position: relative;
        
        &:before {
            content: "";
            border-radius: 20px;
            position: absolute;
            left: 42px;
            right: 42px;
            top: 0;
            height: 100%;
            background-color: $bg-secondary;
         
            z-index: -1;
        }
    }

    &:nth-child(odd) {
        .template-title {
            &:after {
                background-color: $color-primary;
            }
        }
    }
}

.template-title {
    text-align: center;
    position: relative;
    padding-bottom: 18px;
    margin-bottom: 50px;

    h2 {
 
        line-height: 124.5%;
        text-transform: uppercase;
        margin-bottom: 6px;
    }

    h3 {
        font-weight: 300;
        font-size: 24px;
        line-height: 1.25;
    }

    &:after {
        content: "";
        position: absolute;
        left: 50%;
        top: calc(100% - 2px);
        transform: translate(-50%, 0);
        width: 85px;
        height: 2px;
        background-color: $color-primary;
    }

    

    /*type 3*/
    &.col-lg-7 {
        margin-bottom: 35px;
        text-align: left;
        h2 {
            &:after {
                content: "";
                position: static;
                display: inline-block;
                width: 85px;
                height: 2px;
                margin: 10px 10px 10px 19px;
                background-color: $color-secondary;
            }
        }

        &:after {
            display: none;

        }
    }
}

.input-row {
    display: flex;
}

.input-column {
    width: 100%;
}

.input-box {
    .fileLabel{
       border-radius: 10px; 
       border: 1px dashed rgba(255, 255, 255, 0.26);
       padding: 12px 93px;
       text-align: center;
       margin-top: 12px;
       width: 100%;
       transition: 0.2s;
       font-size: 15px;
       min-width: 160px;
       &:hover {
        box-shadow: 0px 0px 4px 2px $color-primary;
       }
   
       &:focus {
           box-shadow: 0px 0px 4px 2px $color-secondary;
       }
   
       &::placeholder {
           color: #CBCBCB;
       }
    }
    margin-bottom: 19px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    position: relative;
    label {
        font-weight: 600;
        font-size: 15px;
        line-height: 126%;
        margin-top: 1px;
        margin-bottom: 6px;
    }

    input {
        border-radius: 10px;
        padding: 17px 20px;
        font-size: 14px;
        min-height: 56px;
        border: none;
        transition: .2s;
        width: 100%;
        
        &:focus-visible {
            outline: none;
        }

        &:hover {
            box-shadow: 0px 0px 4px 2px $color-primary;
        }

        &:focus {
            box-shadow: 0px 0px 4px 2px $color-secondary;
        }

        &::placeholder {
            color: #CBCBCB;
        }
    }
}
